import {
  Component,
  OnInit,
  HostListener,
  ViewChild,
  ElementRef,
} from "@angular/core";
import { CommentsComponent } from "../comments/comments.component";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { PublicacioneServices } from "src/app/service/publicaciones.service";
import { Post } from "src/app/models/post.model";
import { CreatePostComponent } from "../create-post/create-post.component";
import { CommentsService } from "src/app/service/comments.service";
import { AlertsService } from "src/app/service/alerts.service";

@Component({
  selector: "app-publications",
  templateUrl: "./publications.component.html",
  styleUrls: ["./publications.component.scss"],
})
export class PublicationstComponent implements OnInit {
  // Declaracin de variables de clase
  lengthPost: number = 0; // Variable para almacenar el total de publicaciones
  pageSize: number = 5; // Tamao de pgina, determina cuntas publicaciones se muestran por pgina
  currentPage: number = 0; // Pgina actual, rastrea la pgina actual que se est mostrando
  posts: Post[] = []; // Arreglo para almacenar las publicaciones que se cargan

  scrollPosition = 0; // Variable para rastrear la posicin de desplazamiento en la pgina

  // Variables para el control de carga y paginacin
  loading = false; // Indica si se estn cargando ms publicaciones
  cargando = false; //

  loadPostsTimeout: any;
  currentVideoIndex = 0; // Variable para rastrear el ndice del video actual

  // Referencia al elemento de video en el DOM, se utiliza con @ViewChild
  @ViewChild("videoElement") videoElement!: ElementRef;
  // Variable para rastrear el video actual que se est reproduciendo
  private currentPlayingVideo: HTMLVideoElement | null = null;

  // Constructor de la clase, se inyectan el servicio de dilogo (MatDialog) y el servicio de publicaciones (PublicacioneServices)
  constructor(
    public dialog: MatDialog,
    private postService: PublicacioneServices,
    private commentsService: CommentsService,
    private alertsService: AlertsService
  ) {}

  ngOnInit(): void {
    this.loadMorePosts(); // Cargar las primeras publicaciones al iniciar el componente
  }

  // Mtodo para abrir el dilogo de comentarios, se muestra un cuadro de dilogo con los comentarios del post seleccionado
  comenOpen(post: Post) {
    // Objeto donde contiene las propiedad para el modal de comentarios
    const matDialogConfig: MatDialogConfig = {
      maxWidth: "400vw",
      maxHeight: "400vh",
      panelClass: "full-screen-dialog-comment-tokTik",
      data: post,
    };
    // Abriendo Modal
    const modalRef = this.dialog.open(CommentsComponent, matDialogConfig);
    modalRef.afterClosed().subscribe((result) => {});
  }

  // Mtodo para abrir el dilogo para crear una nueva publicacin
  addPost() {
    const modalRef = this.dialog.open(CreatePostComponent, {
      maxWidth: "100vw",
      maxHeight: "100vh",
      panelClass: "full-screen-dialog-comment-tokTik",
    });
    modalRef.afterClosed().subscribe((result) => {
      this.posts = []; // Limpiar el arreglo de publicaciones para cargar nuevos datos
      this.getAllPostPage(0, 1); // 100 posts por carga--SE DEBE CAMBIAR
    });
  }

  // Mtodo para obtener todas las publicaciones de una pgina especfica y mostrarlas en el componente
  getAllPostPage(page: number, size: number) {
    console.log(page, size);
    this.postService.findAllPostTokTikPage(page, size).subscribe({
      next: (data) => {
        this.lengthPost = data.count;
        this.posts = this.posts.concat(data.list); // Concatenar los nuevos posts al final del array existente

        // Agregar eventos 'loadeddata' a todos los videos
        this.addVideoLoadedDataEvents();

        // Realizar el desplazamiento hacia el video recin cargado
        this.scrollToNextVideo();
      },
    });
  }

  // Mtodo para agregar eventos 'loadeddata' a todos los videos
  addVideoLoadedDataEvents() {
    // Esperar a que se carguen todos los videos y luego agregar los eventos 'loadeddata'
    // Esto asegura que los eventos se agreguen a todos los videos despus de que se han renderizado en el DOM
    setTimeout(() => {
      const videoElements = document.querySelectorAll(
        ".video"
      ) as NodeListOf<HTMLVideoElement>;
      videoElements.forEach((video) => {
        video.addEventListener("loadeddata", this.onVideoLoaded.bind(this));
      });
    }, 15);
  }

  // Mtodo que se ejecuta cuando un video se ha cargado
  onVideoLoaded(event: Event) {
    const targetVideo = event.target as HTMLVideoElement;
    targetVideo.scrollIntoView({ behavior: "smooth", block: "end" });
  }

  // Mtodo para realizar el desplazamiento hacia el video actual
  scrollToNextVideo() {
    if (this.videoElement && this.videoElement.nativeElement) {
      this.videoElement.nativeElement.scrollIntoView({ behavior: "smooth" });
    }
  }

  // Mtodo para cargar ms publicaciones
  loadMorePosts() {
    if (!this.loading) {
      this.loading = true;
      this.getAllPostPage(this.currentPage, 1); // 100 posts por carga--SE DEBE CAMBIAR
      this.currentPage++; // Avanzamos al siguiente grupo de 5 posts
      this.currentVideoIndex = this.posts.length - 1; // Actualizar el ndice al final del array

      this.loading = false;
    }
  }

  // Mtodo para manejar el evento de hacer clic en el botn 'LIKE' de una publicacin
  toggleLike(row) {
    console.log(row);
    if (row.liked) {
      console.log("Restando likes");
      // this.postLiked=false;
      row.liked = false;
      row.numLike--;
      this.subtractLike(row.idPost);
    } else {
      // this.postLiked=true;
      row.numLike++;
      row.liked = true;
      console.log("Sumando like");
      this.sumLike(row.idPost);
    }
  }

  // Mtodo para restar likes de una publicacin
  subtractLike(id) {
    this.postService.subtractLike(id).subscribe({
      next: (data) => {
        console.log(data);
      },
    });
  }

  // Mtodo para sumar likes a una publicacin
  sumLike(id) {
    this.postService.sumLike(id).subscribe({
      next: (data) => {
        console.log(data);
      },
    });
  }

  // Mtodo que se ejecuta cuando se inicia la reproduccin de un video
  onVideoPlay(event: Event) {
    const targetVideo = event.target as HTMLVideoElement;

    // Si hay un video reproducindose actualmente, lo detenemos
    if (this.currentPlayingVideo && this.currentPlayingVideo !== targetVideo) {
      this.currentPlayingVideo.pause();
    }

    // Asignamos el video actual como el video que se est reproduciendo
    this.currentPlayingVideo = targetVideo;
  }
 
}
